/**
 * @package buyers-experience
 */
import template from './nwgncy-product-configurator-detail.html.twig';

const {Mixin} = Shopware;
const {Criteria} = Shopware.Data;

// eslint-disable-next-line sw-deprecation-rules/private-feature-declarations
export default {
    template,

    inject: ['repositoryFactory', 'configService', 'acl', 'feature'],

    mixins: [
        Mixin.getByName('placeholder'),
    ],

    created() {
        this.createdComponent();
    },

    data() {
        return {
            productConfigurator: null,
            productConfiguratorForms: null,
            processSuccess: false,
        };
    },

    computed: {
        productConfiguratorRepository() {
            return this.repositoryFactory.create('prod_conf');
        },

        productConfiguratorFormsRepository() {
            return this.repositoryFactory.create('prod_conf_form');
        },

        productConfiguratorCriteria() {
            const criteria = new Criteria(1, 25);
            criteria.addAssociation('ProductConfiguratorForms');
            return criteria;
        },
    },
    methods: {
        createdComponent() {

            this.productConfigurator = this.productConfiguratorRepository.create(Shopware.Context.api);
            this.productConfiguratorForms = this.productConfiguratorFormsRepository.create(Shopware.Context.api);

            this.productConfiguratorRepository
                .search(this.productConfiguratorCriteria, Shopware.Context.api)
                .then((result) => {
                    this.productConfigurator = result;
                    console.log(result);
                });
        },

        onAddButton(ruleId = null){
            this.productConfiguratorForms = this.productConfiguratorFormsRepository.create(Shopware.Context.api);
            console.log(this.productConfiguratorForms);
            console.log(this.productConfigurator);
            this.productConfigurator.ProductConfiguratorForms.add(this.productConfiguratorForms);
        },
        saveFinish() {
            this.processSuccess = false;
        },
        onClickSave() {
            // this.isLoading = true;
            // this.isLoadingData = true;

            this.productConfiguratorRepository
                .save(this.productConfigurator, Shopware.Context.api)
                .then(() => {
                    // this.isLoading = false;
                    // this.isLoadingData = false;
                    // this.processSuccess = true;
                }).catch(() => {
                // this.isLoading = false;
                // this.isLoadingData = false;
            });
        },
    }
};
